import s from "./Dialogs.module.css";
import React from "react";
import TialogItem from "./dialogItem/dialog";
import MessageItem from "./messageItem/message";
import {Field, reduxForm} from "redux-form";
import {Textarea} from "../Common/Textarea/Textarea";
import {maxLengthCreator, requiredField} from "../../utils/validators/validator";

function Dialogs(props) {
    let state = props.dialogUser;

    let dialogsUser = state.dialogsData.map(
        d => <TialogItem name={d.name} id={d.id}/>)

    let dialogsMessage = state.messagesData.map(
        m => <MessageItem message={m.message}/>)

    let messageSend = (values) => {
        props.sendMessage(values.dialogNewMessage)
    }

    return (
        <div className={s.all}>
            <div className={s.dialogs}>
                <div className={s.users}>
                    <div>
                        {dialogsUser}
                    </div>
                </div>

                <div className={s.messages}>
                    <div>
                        {dialogsMessage}
                    </div>
                </div>
            </div>
            <div>
                <DialogsRedux onSubmit={messageSend}/>
            </div>
        </div>
    )
}

export const DialogsForm = (props) => {
    const maxLength = maxLengthCreator(10);
    return (
        <form onSubmit={props.handleSubmit}>
            <div>
                <div><br/>
                    <Field component={Textarea} name={"dialogNewMessage"} placeholder={"Enter your message"}
                    validate={[requiredField, maxLength]}/>
                </div>
                <button>Отправить сообщение</button>
            </div>
        </form>
    )
}

export const DialogsRedux = reduxForm({
    form: "Dialogs"
})(DialogsForm)

export default Dialogs;

